.sn-panel {
  padding: 5px;
  background: var(--sn-primary-light-bg-color);
  color: var(--sn-primary-light-text-color); }
  .sn-panel .sn-separator {
    margin: 5px 10px;
    border-right: 2px solid var(--sn-primary-light-border-color); }
  .sn-panel.vertical .sn-separator {
    border-right: none;
    border-bottom: 2px solid var(--sn-primary-light-border-color);
    margin: 10px 5px; }



